body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #4B2D73;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
* Custom Background Colors
*/

.bg-color--1 {
	background-color: #1C053A;
}

.bg-color--2 {
	background-color: #311557;
}

.bg-color--3 {
	background-color: #4B2D73;
}

.bg-color--4 {
	background-color: #6B4E90;
}

.bg-color--5 {
	background-color: #8F78AD;	
}

.bg-color--none {
	background-color: rgba(0,0,0,0);
}

.bg-color--black-t1 {
	background-color: rgba(255,255,255,0.1);
}

.bg-color--inherit {
	background-color: inherit;
}


/*
* Text Colors
*/

.color--1 {
	color: #1C053A;
}

.color--2 {
	color: #311557;
}

.color--3 {
	color: #4B2D73;
}

.color--4 {
	color: #6B4E90;
}

.color--5 {
	color: #8F78AD;	
}

.color--1a {
	color: #1a1a1a;
}

.color--b {
	color: #bbbbbb;
}

/*
* Custom Outline Colors
*/

.outline--none {
	outline: none;
}

/*
* Other
*/
.hover--2:hover {
	background-color: #311557;
}

.border--1 {
	border-radius: 1px;
}

.border-color--2 {
	border-color: #1C053A;
}

.tlc {
	text-align-last: center;
}

.dropdown-none {
	-webkit-appearance: none;
	-moz-appearance: none;
}

.dropdown-none::-ms-expand {
	display: none;
}

.list--ml0 {
	margin-left: 0px;
	-webkit-padding-start: 0px;
}
.Play {
	position: relative;
}

.Tile {
	width: 25%;
	height: 25%;
}

.Play__timer {
	position: absolute;
	z-index: 2;
	top: 0;
	width: 100%;
}
.ViewLayer {
	width: 256px;
	height: 512px;
}

@media only screen and (min-width: 768px) {
	.ViewLayer {
		width: 325px;
		height: 650px;
	}
} 
